import { useMutation } from 'react-query';
import AuthLayout from '../components/auth/AuthLayout';
import PageTitle from '../components/PageTitle';
import FormBox from '../components/auth/FormBox';
import Input from '../components/auth/Input';
import Button from '../components/auth/Button';
import Separator from '../components/auth/Separator';
import BottomBox from '../components/auth/BottomBox';
import routes from '../routes';
import FormError from '../components/auth/FormError';
import { useForm } from 'react-hook-form';
import { Title } from './Login';
import { useHistory } from 'react-router-dom';
import { createUserAPI } from '../lib/api/auth';

interface SignUpFormField {
  email: string;
  name: string;
  password: string;
}

interface Props {
  darkModeInput: [boolean, () => void];
}

export default function SignUp({ darkModeInput }: Props) {
  const history = useHistory();
  const { mutateAsync, isLoading } = useMutation(createUserAPI);

  const {
    register,
    handleSubmit,
    formState: { errors, isValid },
  } = useForm<SignUpFormField>({
    mode: 'onChange',
  });

  const onSubmitValid = async (data: SignUpFormField) => {
    if (isLoading) return;
    console.log(data);
    const { email, name, password } = data;
    try {
      const res = await mutateAsync({
        email,
        name,
        password,
      });
      console.log(res);
      if (res.data.ok) {
        history.push(routes.home, {
          message: ' .  .',
          email,
          password,
        });
      } else {
        alert(res.data.error);
      }
    } catch (error) {
      alert(error);
    }
  };

  return (
    <AuthLayout darkModeInput={darkModeInput}>
      <PageTitle title="Sign up" />
      <FormBox>
        <Title> </Title>
        <h2>  , !</h2>
        <Separator />
        <form onSubmit={handleSubmit(onSubmitValid)}>
          <Input
            {...register('email', {
              required: {
                value: true,
                message: ' .',
              },
              minLength: {
                value: 5,
                message: ' 5  .',
              },
            })}
            type="text"
            placeholder=" "
            hasError={Boolean(errors?.email)}
          />
          <FormError message={errors?.email?.message} />
          <Input
            {...register('name', {
              required: {
                value: true,
                message: ' .',
              },
            })}
            type="text"
            placeholder=""
            hasError={Boolean(errors?.name)}
          />
          <FormError message={errors?.name?.message} />
          <Input
            {...register('password', {
              required: {
                value: true,
                message: ' .',
              },
              minLength: {
                value: 5,
                message: ' 5  .',
              },
            })}
            type="password"
            placeholder=""
            hasError={Boolean(errors?.password)}
          />
          <FormError message={errors?.password?.message} />
          <Button disabled={!isValid} type="submit">
            
          </Button>
          {isLoading && <p>..</p>}
        </form>
      </FormBox>
      <BottomBox
        title=" ?"
        description=""
        link={routes.home}
      />
    </AuthLayout>
  );
}
