export const monthStr = [
  '',
  'Jan',
  'Feb',
  'March',
  'April',
  'May',
  'June',
  'July',
  'Aug',
  'Sept',
  'Oct',
  'Nov',
  'Dec',
];

export const dayStr = ['SUN', 'MON', 'TUE', 'WED', 'THU', 'FRI', 'SAT'];

export function dateWithDay(dateStr: string): string {
  const dateObj = new Date(dateStr);
  const day = dateObj.getDay();
  const date = dateObj.getDate();
  let dateExtension = 'th';
  if (date == 1) dateExtension = 'st';
  else if (date == 2) dateExtension = 'nd';
  else if (date == 3) dateExtension = 'rd';
  return `${dayStr[day]}, ${date}${dateExtension}`;
}

export function priceToString(price: number): string {
  return price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
}

export const getPaymentEnKeyName = (paymentName: string) => {
  const pName = paymentName.toLowerCase();

  if (pName.includes('') || pName.includes('kakao')) {
    return 'kakao';
  } else if (pName.includes('') || pName.includes('shinhan')) {
    return 'shinhan';
  } else if (pName.includes('') || pName.includes('samsung')) {
    return 'samsung';
  } else if (pName.includes('') || pName.includes('lotte')) {
    return 'lotte';
  } else if (pName.includes('') || pName.includes('woori')) {
    return 'woori';
  } else if (pName.includes('') || pName.includes('cash')) {
    return 'cash';
  } else if (pName.includes('') || pName.includes('hyundai')) {
    return 'hyundai';
  } else if (pName.includes('') || pName.includes('bc')) {
    return 'bc';
  } else {
    return '';
  }
};
