import React, { useState, useEffect } from 'react';
import SwipeableViews from 'react-swipeable-views';
import styled from 'styled-components';
import { priceToString } from '../../lib/helper';
import '../calendar/CardSlider.css';

const left = '<';
const right = '>';

const style = {
  animation: 'float 2s ease-in-out infinite',
  border: 'solid black 4px',
  color: 'white',
};

interface Props {
  accountList: string[];
  account: string;
  accountId: string;
  setAccount: React.Dispatch<React.SetStateAction<string>>;
  balanceArr: string[];
}

const SendCardSlider = ({
  accountList,
  account,
  accountId,
  setAccount,
  balanceArr,
}: Props) => {
  const [cardIndex, setCardIndex] = useState(+accountId);

  //
  useEffect(() => {
    setCardIndex(+accountId);
  }, [accountId]);

  //   
  const changeIndex_minus = () => {
    setCardIndex(cardIndex - 1);
  };

  //   
  const changeIndex_plus = () => {
    setCardIndex(cardIndex + 1);
  };

  let cardName: string[] = [];
  accountList.map((el) => {
    if (el.includes('') || el.includes('shinhan')) {
      cardName.push('shinhan');
    } else if (el.includes('') || el.includes('samsung')) {
      cardName.push('samsung');
    } else if (el.includes('') || el.includes('kakao')) {
      cardName.push('kakao');
    } else if (el.includes('') || el.includes('lotte')) {
      cardName.push('lotte');
    } else if (el.includes('') || el.includes('woori')) {
      cardName.push('woori');
    } else if (el.includes('') || el.includes('cash')) {
      cardName.push('cash');
    } else if (el.includes('') || el.includes('hyundai')) {
      cardName.push('hyundai');
    } else if (el.includes('') || el.includes('bc')) {
      cardName.push('bc');
    } else {
      cardName.push('default');
    }
  });

  return (
    <SwipeableViews
      enableMouseEvents
      index={cardIndex}
      disabled={Boolean(true)} //swipe 
    >
      {accountList.map((el, i) => (
        <>
          <div className="contain">
            {i >= 1 ? (
              <Button onClick={changeIndex_minus}>{left}</Button>
            ) : null}
            <button
              className={`card0 ${cardName[i] + '0'}`}
              style={el === account ? style : undefined}
              onClick={() => setAccount(el)}
            >
              {el}
            </button>
            {i < accountList.length - 1 ? (
              <Button onClick={changeIndex_plus}>{right}</Button>
            ) : null}
          </div>
          <div className="balance">
            {' '}
             : {priceToString(+balanceArr[i])}
          </div>
        </>
      ))}
    </SwipeableViews>
  );
};

const Button = styled.button`
  border: none;
  background-color: inherit;
  color: black;
  font-size: 45px;
  cursor: pointer;
  display: inline-block;
  vertical-align: middle;
`;

export default SendCardSlider;
